import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

export default new VueRouter({
	mode: 'history',
  	routes: [
			{
					path: '/',
					name: 'home',
					// meta: { layout: "default" },
					component: () => import("@/views/Home")
			},
			{
					path: '/leadmagnets/add_lead/rahmet',
					name: 'rahmet',
					// meta: { layout: "default" },
					component: () => import("@/views/Rahmet")
			},
    	{
    		path: '/leadmagnets/add_lead',
      		name: 'my_lead',
      		component: () => import("@/views/Home")
    	}
  	]
})



